/* base text block */
const baseTextBlock = () => ({
  text1: "",
  text2: "",
  text3: "",
  visible: false,
});

/* base doc */
const baseDoc = () => ({
  id: "",
  name: "",
  slug: "",
  description: "",
  variantNote: "",
  activeOrderLimit: "",
  edt: "",
  image: "",
  inflationPercentage: 0,
  basePriceMultiplier: 0,
  lockedImage: "",
  lockedImages: [],
  lockedText: "",
  mainTextBlock: baseTextBlock(),
  lock: false,
  softLock: false,
  askForPrice: false,
  status: false,
  category: "",
});

export default {
  props: {
    model: String,
    collectionCategories: Array,
  },
  data() {
    return {
      editMode: false,
      doc: baseDoc(),
      loading: false,
      updated: false,
      pricesReset: false,
      totalProductsReset: 0,
      fetchedInflationPercentage: -1,
      fetchedBasePriceMultiplier: 0
    };
  },
  computed: {
    inflationChanged() {
      return this.fetchedInflationPercentage != this.doc.inflationPercentage;
    },
    basePriceMultiplierChanged() {
      console.log(this.fetchedBasePriceMultiplier, this.doc.basePriceMultiplier)
      return this.fetchedBasePriceMultiplier != this.doc.basePriceMultiplier;
    },
  },
  methods: {
    async resetProductPrices() {
      const request = await this.$post("/updateNonINRPricing", {
        collectionId: this.doc._id,
        inflationPercentage: this.doc.inflationPercentage,
        type: "collection",
      });

      /* show feed on update */
      if (request.resolved === true) {
        /* save document */
        this.updateDocument();
        this.pricesReset = true;
        this.totalProductsReset = request.response.total;
        setTimeout(() => (this.pricesReset = false), 4500);
      }
    },
    imageListUpdated(list, type) {
      switch (type) {
        case "image":
          this.doc.image = list.length > 0 ? list[0].path : "";
          break;
        case "lockedImage":
          this.doc.lockedImage = list.length > 0 ? list[0].path : "";
          break;
        case "lockedImages":
          console.log("locked image set", list.length);
          this.doc.lockedImages = list;
          break;
      }
    },
    async updateDocument(payload) {
      this.loading = true;
      /* reset values if needed */
      // if(this.basePriceMultiplierChanged) {
      //   this.doc.inflationPercentage = 0;
      // }
      // if(this.inflationChanged) {
      //    this.doc.basePriceMultiplier = 0;
      // }
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode,
        { dontSkipBasePriceMultiplierUpdate: this.basePriceMultiplierChanged }
      );
      this.loading = false;

      if (!result.updated) return;

      this.$emit("updated");
      this.populateForm(result.doc);
      this.$flash(this);
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
    populateForm(details) {
      const {
        _id,
        name,
        slug,
        description,
        variantNote,
        edt,
        activeOrderLimit,
        image,
        inflationPercentage,
        basePriceMultiplier,
        lockedImage,
        lockedImages,
        lockedText,
        mainTextBlock,
        lock,
        softLock,
        status,
        category,
        askForPrice,
      } = details;

      this.doc = {
        _id,
        name,
        slug,
        description,
        variantNote,
        activeOrderLimit,
        edt: edt !== null ? edt.toString() : "",
        image,
        inflationPercentage:
          inflationPercentage == undefined ? 0 : inflationPercentage,
        basePriceMultiplier:
          basePriceMultiplier === undefined ? 0 : basePriceMultiplier,
        lockedImage,
        lockedImages: lockedImages || [],
        lockedText,
        mainTextBlock:
          mainTextBlock === undefined ? baseTextBlock() : mainTextBlock,
        lock,
        softLock,
        status,
        category: category === undefined ? "" : category,
        askForPrice: askForPrice === undefined ? false : askForPrice,
      };
      /* set initailly fetched percentage */
      this.fetchedInflationPercentage = this.doc.inflationPercentage;
      this.fetchedBasePriceMultiplier = this.doc.basePriceMultiplier;
      this.editMode = true;
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      this.$refs.imageUploader.clearFileSelection();
      // this.$refs.lockedImageUploader.clearFileSelection();
      this.$refs.lockedImagesUploader.clearFileSelection();
      this.populateForm(baseDoc());
      this.editMode = false;
    },
  },
};
